div.rock {
    text-align: center;
    color: #555
}

.rock h3 {
    margin: 1%;
}

.rock .hand,
.computerChoice {
    width: 10%;
    margin: 2%;
    border-radius: 50%;
    cursor: pointer;
}

.rock .hand:hover {
    transform: scale(1.05);
}

.rock button {
    position: absolute;
    top: 60%;
    left: 30%;
    font-size: 24px;
    padding: 10px 25px;
    border-radius: 5px;
    color: white;
    background-color: rgba(43, 209, 126);
    cursor: pointer;
    transition: 0.2s;
    outline: none;
}

.rock button:hover {
    background-color: rgb(37, 161, 114);
}

.rock .statistics {
    position: absolute;
    top: 70%;
    left: 30%;
    font-size: 24px;
    text-align: center;
}

.rock .gameresult {
    position: absolute;
    top: 70%;
    right: 20%;
    font-size: 24px;
    text-align: center;
}



.rock .statistics h4,
.rock .gameresult h4 {
    margin: 2%;
}